<template>
    <div id="login">
        <div id="login01">
            <img src="http://cdn.xqit.net/ht/img/loginframe.png" alt="" class="login01_img">
            <ul>
                <li class="name_input">
                    <input type="text" ref="name" onkeyup="value=value.replace(/[^\w\.\/]/ig,'')">
                    <img class="img" src="http://cdn.xqit.net/ht/img/login3.png" />
                </li>
                <li class="word_input">
                    <input type="password" ref="word" onkeyup="value=value.replace(/[^\w\.\/]/ig,'')">
                    <img class="img" src="http://cdn.xqit.net/ht/img/password3.png" />
                </li>
                <li class="login_input">
                    <button @click="jump">登陆</button>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'login',
    data(){
        return{
            
        }
    },
    methods:{
        jump(){
            var that = this;
            var name = this.$refs.name.value;
            var word = this.$refs.word.value;
            let formData = new FormData();
            formData.append('username',name);
            formData.append('password',word);
            if(name == "" || name == null){
                this.$message.error('账号不能为空')
            }else if(word == "" || word == null){
                this.$message.error('密码不能为空')
            }else{
                axios.post(this.baseUrl+'/ydjxzg/htlog/htlogin.action', formData)
                .then(function (res) {
                    console.log(res);
                    if(res.data.mess == true){
                        that.$message.success('登陆成功')
                        that.$router.push({path: '/index'})
                    }else{
                        that.$message.error('账号或密码输入有误')
                    }
                })
            }
        }
    }
}
</script>

<style>
.login01_img{
    position: absolute;
    width: 300px;

}
#login01{
    /* background-image: url("http://cdn.xqit.net/ht/img/loginframe.png"); */
    width: 300px;
    height: 300px;
    /* position: absolute; */
    margin: 10% 0 0 70%;
}
#login{
    position: fixed;
    width: 100%;
    height: 100%;
    background-image: url("http://cdn.xqit.net/ht/img/loginhoutai.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}
#login ul{
    width: 60%;
    font-size: 40px;
    position: relative;
    /* right: 0; */
    left: 40px;
    top: 70px;
    /* margin: 0 0 0 10%; */
    /* margin: 18% 17.2% 0 0; */
    /* margin: 18% 0 0 59%; */
}
/* #login ul li{
    position: absolute;
    margin-top: 35px;
    width: 320px;
    width: 20%;
    float: right;
} */
/* .name_input{
    margin-top: 0;
}
.word_input{
    margin-top: 5%;
}
.login_input{
    margin-top: 10%;
    width: 100%;
} */
#login ul li input{
    width: 89%;
    height: 30px;
    background-color:transparent;
    border: none;
    border-bottom: 3px #ffffff solid;
    color:#ffffff;
    caret-color:#ffffff;
    padding-left: 36px;
    outline:none;
    font-size: 18px;
}
#login ul li button{
    width: 100%;
    height: 36px;
    /* margin-top: 26px; */
    margin: 20px auto;
}
.img{
    float: left;
    margin-top: -36px;
    width: 25px;
    height: 25px;
}
</style>



